.why-us {
   position: relative;
   padding: 100px 0 0 0;
   background-color: $color-dark-blue;

   &__decor-lines-top-to-bottom {
      top: 200px;
      right: 0;
   }

   &__title {
      max-width: 800px;
      margin: 0 auto 100px auto;
   }

   &__items {}

   &__item {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 80px;
      
      &:not(:last-child) {
         margin: 0 0 120px 0;

         @media (max-width: 776px) {
            border-bottom: 1px solid $color-light-gray;
            margin: 0 0 60px 0;
         }
      }
   }

   &__img {
      
      @media (max-width: 1204px) {
         width: 40%;
      }

      @media (max-width: 776px) {
         display: none;
      }
   }

   &__text {

      &__title {
         font-weight: 500;
         font-size: 28px;
         margin: 0 0 20px 0;

         @media (max-width: 400px) {
            font-size: 22px;
         }
      }

      &__subtitle {
         font-weight: 300;
         line-height: 30px;
         max-width: 580px;
         margin: 0 0 50px 0;

         @media (max-width: 400px) {
            font-size: 16px;
            line-height: 20px;
         }
      }

      &__btn {
         
         @media (max-width: 776px) {
            display: none;
         }
      }
   }
}