.faq {
   position: relative;
   background-color: $color-dark-blue;
   padding: 170px 0 0 0;

   &__decor-lines-left-to-right {
      top: 0;
      left: 0;
   }

   &__decor-lines-right-to-left {
      top: 0;
      right: 0;
   }

   &__title {
      margin: 0 0 60px 0;
   }

   &__accordion {
      position: relative;
      max-width: 890px;
      width: 100%;
      margin: 0 auto;
      z-index: 2;

      &-content {
         background-color: $color-portgore;
         border: 1px solid $color-dark-lilac;
         border-radius: 12px;
         transition: border .4s, box-shadow .4s;

         &.active {
            border: 1px solid $color-light-lilac;
            box-shadow: 0px 0px 10px 4px rgba(115, 115, 177, 1);
         }
         
         &:not(:last-child) {
            margin: 0 0 20px 0;
         }

         &__title {
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-weight: 600;
            font-size: 22px;
            padding: 40px;
            min-height: 100px;
            cursor: pointer;

            @media (max-width: 600px) {
               font-size: 20px;
               padding: 30px;
            }

            @media (max-width: 500px) {
               font-size: 18px;
               padding: 20px;
            }

            @media (max-width: 400px) {
               font-size: 16px;
               padding: 16px;
            }
   
            &-img {
               max-width: 28px;
               width: 100%;
               height: 16px;
               margin: 0 0 0 20px;
               transition: transform .4s;

               @media (max-width: 500px) {
                  max-width: 20px;
               }
            }
         }

         &.active {

            .faq__accordion-content__title-img {
               transform: rotate(180deg);
            }
         }
   
         &__text {
            font-weight: 300;
            line-height: 30px;
            color: $color-light-gray;
            padding: 0 40px;
            transition: height .3s linear;

            @media (max-width: 600px) {
               padding: 0 30px;
               font-size: 16px;
            }

            @media (max-width: 500px) {
               padding: 0 20px;
               line-height: 20px;
            }

            @media (max-width: 400px) {
               padding: 0 16px;
               font-size: 14px;
            }
         }
      }
   }

   .accordion {

      &-content {
         overflow: hidden;

         &__title {
            
         }
   
         &__text {
            height: 0;
         }

         &.active {

            .accordion-content__text {
               margin-bottom: 20px;
            }
         }
      }
   }
}