.hero {
   background-image: url('../img/hero-bg.jpg');
   background-position: center;
   background-repeat: no-repeat;
   background-size: cover;
   position: relative;
   padding: 158px 0 0 0; //158px height header
   height: 100%;
   z-index: 5;

   @media (max-width: 768px) {
      padding: 178px 0 0 0;
   }

   &__conteiner {
      position: relative;
      display: flex;
      flex-direction: column;
      min-height: 100%;
   }

   &__title {
      font-weight: 500;
      font-size: 50px;
      line-height: 78px;
      text-align: center;
      max-width: 940px;
      margin: 0 auto 40px auto;

      @media (max-width: 768px) {
         font-size: 38px;
         line-height: 50px;
      }

      @media (max-width: 564px) {
         margin: 0 auto 20px auto;
      }

      @media (max-width: 440px) {
         font-size: 28px;
         line-height: 40px;  
      }

      @media (max-width: 320px) {
         font-size: 24px;
      }

      &_gradient {
         position: relative;
         font-weight: 700;
         background-image: linear-gradient(93.89deg, $color-gradient-red -1.38%, $color-gradient-purple 45.84%, $color-gradient-orange 100.94%);
         -webkit-background-clip: text;
         -webkit-text-fill-color: transparent;

         &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 20px;
            right: 0;
            bottom: -10px;
            background-image: url('../img/title-gradient.png');
            background-position: center bottom;
            background-repeat: no-repeat;
            background-size: contain;
         }
      }
   }

   &__subtitle {
      line-height: 30px;
      text-align: center;
      max-width: 730px;
      margin: 0 auto 60px auto;
      color: $color-light-gray;

      @media (max-width: 768px) {
         font-size: 16px;
         line-height: 20px;
      }

      @media (max-width: 440px) {
         font-size: 14px;  
      }
   }

   &__button {
      padding: 30px 40px;
      margin: 0 auto 50px auto;

      @media (max-width: 420px) {
         font-size: 12px;
         padding: 20px 22px;  
      }

      @media (max-width: 320px) {
         font-size: 12px;
         padding: 18px 20px;  
      }
   }

   &__list {
      display: flex;
      margin: auto auto 50px auto;

      @media (max-width: 772px) {
         display: grid;
         grid-template: 1fr 1fr / 1fr 1fr;
         gap: 20px;
      }
   }

   &__item {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 0 40px;

      @media (max-width: 1230px) {
         padding: 0 20px;  
      }

      @media (max-width: 992px) {
         text-align: center;
      }

      @media (max-width: 772px) {
         padding: 0;  
      }

      &:not(:last-child) {
         border-right: 1px solid $color-light-blue;

         @media (max-width: 772px) {
            border: none;
         }
      }
   }

   &__item-title {
      font-weight: 300;
      font-size: 50px;
      margin: 0 0 20px 0;

      @media (max-width: 1230px) {
         font-size: 38px;  
      }

      &_lowercase {
         font-size: 40%;
      }
   }

   &__item-subtitle {
      color: $color-light-gray;
      margin: 0;

      @media (max-width: 1230px) {
         font-size: 14px;  
      }
   }

   .hero__scroll {
      position: absolute;
      bottom: 60px;
      left: 20px;

      @media (max-width: 992px) {
         display: none;
      }
   }
}