.footer {
   position: relative;
   padding: 90px 0 55px 0;
   background-color: $color-dark-blue;

   &__decor-lines-left-to-top {
      left: 0;
      bottom: 0;
   }

   &__decor-lines-right-to-bottom {
      right: 0;
      bottom: 0;
   }

   &__conteiner {
      display: flex;
      justify-content: space-between;

      @media (max-width: 1400px) {
         display: grid;
         grid-template: 1fr / 1fr 1fr;
      }

      @media (max-width: 1000px) {
         display: flex;
         flex-direction: column;
         align-items: center;
      }
   }

   &__info {
      margin: 0 60px 0 0;

      @media (max-width: 1000px) {
         margin: 0 auto 50px auto;
         display: flex;
         flex-direction: column;
         align-items: center;
      }

      &__logo {
         margin: 0 0 30px 0;
      }
      
      &__list {
         margin: 0 0 30px 0;
         
         @media (max-width: 1000px) {
            display: flex;
            flex-direction: column;
            align-items: center;
         }
      }

      &__item {
         display: flex;
         align-items: center;
         font-weight: 500;
         font-size: 16px;
         text-decoration: underline;
         color: $color-light-gray;

         &:not(:last-child) {
            margin: 0 0 30px 0;

            @media (max-width: 500px) {
               margin: 0 0 20px 0;
            }
         }
         
         &__img {
            max-width: 20px;
            height: 20px;
            width: 100%;
            margin: 0 10px 0 0;
         }

         .footer__info__link {
            display: flex;
            align-items: center;
         }
      }

      &__copyright {
         font-weight: 500;
         font-size: 14px;
         color: $color-light-lilac;
      }
   }

   &__menu {
      display: grid;
      grid-template: 1fr / 1fr 1fr;
      gap: 40px;
      margin: 0 60px 0 0;

      @media (max-width: 1000px) {
         margin: 0 auto 50px auto;
      }

      @media (max-width: 500px) {
         display: flex;
         flex-direction: column;
      }

      &__title {
         font-weight: 600;
         font-size: 18px;
         margin: 0 0 20px 0;
         
         @media (max-width: 1000px) {
            text-align: center;
         }
      }

      &__list {
         display: flex;
         flex-direction: column;
      }

      &__item {
         
         @media (max-width: 1000px) {
            text-align: center;
         }

         &:not(:last-child) {
            margin: 0 0 20px 0;

            @media (max-width: 500px) {
               margin: 0 0 10px 0;
            }
         }
      }

      &__link {
         font-weight: 500;
         font-size: 14px;  

         &:hover {
            background-image: linear-gradient(93.89deg, $color-gradient-red -1.38%, $color-gradient-purple 45.84%, $color-gradient-orange 100.94%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
         }
      }
   }

   &__contact {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      @media (max-width: 1400px) {
         grid-column: 1 / span 2;
         margin: 0 auto;
         align-items: center;
      }

      &__tel {
         font-weight: 600;
         font-size: 20px;
         margin: 0 0 10px 0;
      }

      &__time {
         font-weight: 600;
         font-size: 14px;
         color: $color-light-lilac;
         text-transform: uppercase;
         margin: 0 0 20px 0;
      }

      &__btn {
         display: flex;
         align-items: center;
         margin: 0 0 25px 0;

         &__img {
            max-width: 22px;
            height: 22px;
            width: 100%;
            margin: 0 5px 0 0;
         }
      }

      &__text {
         font-weight: 600;
         font-size: 16px;
         margin: 0 0 20px 0;
      }
   }
}