.questions {
   position: relative;
   padding: 270px 0 160px 0;
   background-color: $color-dark-blue;

   &__decor-lines-right-to-bottom {
      top: 0;
      right: 0;
   }

   &__decor-plus {
      right: -100px;
      bottom: -80px;
   }

   &__conteiner {}

   &__box {
      position: relative;
      display: flex;
      max-width: 1170px;
      width: 100%;
      margin: 0 auto;
      padding: 60px 40px 60px 90px;
      background-color: rgb(24, 24, 39, .2);
      box-shadow: 0px 14px 44px #02040F, inset 0px 10px 62px #30385F;
      border-radius: 130px 20px 20px 20px;

      @media (max-width: 600px) {
         padding: 50px 30px;
      }

      @media (max-width: 500px) {
         border-radius: 100px 20px 20px 20px;
      }

      @media (max-width: 400px) {
         padding: 25px 15px;
      }
   }

   &__content {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      &__title {
         font-weight: 600;
         font-size: 36px;
         margin: 0 0 40px 0;
         max-width: 634px;

         @media (max-width: 700px) {
            text-align: center;
         }

         @media (max-width: 500px) {
            font-size: 26px;
         }

         @media (max-width: 400px) {
            font-size: 18px;
         }
      }

      &__list {
         margin: 0 0 50px 0;
      }

      &__item {
         font-weight: 600;
         padding: 5px 0 5px 50px;
         background-image: url(../img/list-element.png);
         background-repeat: no-repeat;
         background-position: left;

         &:not(:last-child) {
            margin: 0 0 20px 0;
         }

         @media (max-width: 600px) {
            font-size: 16px;
         }

         @media (max-width: 500px) {
            padding: 50px 0 0 0;
            background-position: top;
            text-align: center;
            font-size: 14px;
         }
      }

      &__btn {
         
         @media (max-width: 700px) {
            margin: 0 auto;
         }

         @media (max-width: 400px) {
            font-size: 12px;
         }
      }
   }

   &__img {
      position: absolute;
      right: 0;
      bottom: 0;

      @media (max-width: 1200px) {
         display: none;
      }
   }
}