html {
   box-sizing: border-box;
   scroll-behavior: smooth;
}

*, *::after, *::before {
   box-sizing: inherit;
   margin: 0;
}

body {
   margin: 0;
}

img {
   max-width: 100%;
}

ul {
   list-style: none;
   padding: 0;
}

button {
   cursor: pointer;
}

a {
   text-decoration: none;
   color: inherit;
}



html, body {
   height: 100%;
}

body {
   font-family: 'Montserrat', sans-serif;
   font-size: 18px;
   font-weight: 400;
   color: $color-white;
}

.wrapper {
   overflow: hidden;
   display: flex;
   flex-direction: column;
   min-height: 100%;
}

.main {
   flex: 1 1 auto;
}

.conteiner {
   max-width: 1720px;
   padding: 0 20px;
   margin: 0 auto;
}



/*======== RESPONSIVE ========*/

@media (max-width: 1200px) {
   .conteiner {
      max-width: 970px;
   }
}

@media (max-width: 992px) {
   .conteiner {
      max-width: 750px;
   }
}

@media (max-width: 767px) {
   .conteiner {
      max-width: none;
   }
}