.contacts {
   position: relative;
   z-index: 2;

   @media (max-width: 1000px) {
      background-color: $color-dark-blue;
   }

   &__map {
      width: 100%;
      height: 80vh;

      @media (max-width: 1000px) {
         display: none;
      }
   }
   
   &__content {
      position: absolute;
      margin: 10px 0 0 10px;
      max-width: 390px;
      width: 100%;
      box-shadow: 0px 0px 12px 3px $color-gradient-red,
            inset 0px 0px 0px 2px $color-gradient-purple;
      background-color: $color-white;
      border-radius: 40px;
      padding: 40px;
      color: $color-dark-blue;

      @media (max-width: 1000px) {
         position: static;
         margin: 0 auto;
         background-color: $color-dark-blue;
         color: $color-white;
      }

      @media (max-width: 450px) {
         padding: 20px;
      }
   }

   &__title {
      font-weight: 600;
      font-size: 36px;
      margin: 0 0 30px 0;
   }

   &__small-title {
      font-weight: 300;
      font-size: 16px;
      margin: 0 0 15px 0;
   }

   &__subtitle {
      font-weight: 500;
      margin: 0 0 40px 0;
      max-width: 300px;

      &__tel {
         font-size: 22px;
      }

      &__time {
         font-weight: 400;
         font-size: 14px;

         &_bold {
            font-weight: 600;
         }
      }
   }

   &__link {
      font-weight: 500;
      font-size: 20px;
      background-image: linear-gradient(93.89deg, $color-gradient-red -1.38%, $color-gradient-purple 45.84%, $color-gradient-orange 100.94%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
   }
}
