.header {
   background: transparent;
   padding: 45px 0;
   position: absolute;
   top: 0;
   left: 0;
   width: 100%; 
   height: 30%;
   z-index: 99;
   overflow: hidden;
   transition: height .6s ease-out;

   &.height {
      height: 100%;
      overflow-y: scroll;
      overflow-x: hidden;
   }

   @media (max-width: 425px) {
      padding: 18px 0;
   }

   &__conteiner {
      position: relative;
      display: flex;
      align-items: center;

      @media (max-width: 1200px) {
         justify-content: space-between;
      }
   }

   .logo {
      display: flex;
      align-items: center;
      max-width: 320px;
      
      &__text {
         font-weight: 500;
         display: flex;
         flex-direction: column;
         align-items: center;

         &_top {
            font-size: 24px;
            margin: 0 0 10px 0;
         }
      
         &_bottom {
            font-size: 10px;
            text-transform: uppercase;
         }  
      }

      img {
         width: 60px;
         height: 60px;
         object-fit: cover;
         margin: 0 10px 0 0;

         @media (max-width: 375px) {
            width: 30px;
            height: 30px;
         }
      }
   }

   .header__logo .logo__text {
      @media (max-width: 768px) {
         display: none;
      }
   }

   &__nav {
      margin: 0 160px 0 auto;

      @media (max-width: 1400px) {
         margin: 0 100px 0 auto;
      }

      @media (max-width: 1300px) {
         margin: 0 60px 0 auto;
      }

      @media (max-width: 1200px) {
         display: none;
      }
   }

   .nav {

      &__list {
         display: flex;

         @media (max-width: 1200px) {
            flex-direction: column;
         }
      }
   
      &__item:not(:last-child) {
         margin: 0 60px 0 0;

         @media (max-width: 1300px) {
            margin: 0 30px 0 0;
         }
      }
   
      &__link {
         font-size: 16px;
         color: $color-light-gray;
         transition: color .3s;

         &:hover {
            color: $color-white;
         }
      }
   }

   &__info {
      display: flex;
      align-items: center;
      margin: 0 50px 0 0;

      @media (max-width: 425px) {
         text-align: center;
      }
   }

   &__info {

      .info__img {

         @media (max-width: 768px) {
            display: none;
         }
      }
   }

   .info {

      &__img {
         margin: 0 18px 0 0;
         width: 40px;
         height: 40px;
      }
   
      &__box {
         display: flex;
         flex-direction: column;
      }
   
      &__tel {
         margin: 0 0 10px 0;
         font-weight: 800;

         @media (max-width: 425px) {
            font-size: 14px;
            margin: 0 0 6px 0;
         }
      }
   
      &__time {
         color: $color-light-gray;
         font-size: 14px;

         @media (max-width: 425px) {
            font-size: 12px;
         }

         &_bold {
            font-weight: 600;
         }
      }
   }

   .header__button {

      @media (max-width: 1200px) {
         display: none;
      }
   }

   .burger {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 21px;
      height: 20px;
      cursor: pointer;
      z-index: 100;
      display: none;
		
      &__line {
         position: relative;
         width: 100%;
         height: 2px;
         background-color: $color-white;
         border-radius: 2px;
         transition: transform .5s;
         
			&_short {
            width: 14px;
            margin-left: auto;
            transform: translateX(0);
         }
		}

      @media (max-width: 1200px) {
         display: flex;

         &.active span:nth-of-type(1) {
            top: 8px;
            transform: rotate(46deg);
            transition: transform .5s;
         }
      
         &.active span:nth-of-type(2) {
            transform: translateX(200px);
            transition: transform .5s;
         }
      
         &.active span:nth-of-type(3) {
            bottom: 10px;
            transform: rotate(132deg);
            transition: transform .5s;
         }
      }
   }
   


   &__menu {
      position: absolute;
      width: 100%;
      min-height: 100%;
      padding: 43px 0;
      top: 0;
      left: 0;
      background-color: $color-dark-blue;
      transform: translateX(1200px);
      transition: transform .6s ease-in-out;
      z-index: 99;

      @media (max-width: 772px) {
         transform: translateX(772px);
      }

      @media (max-width: 600px) {
         transform: translateX(600px);
      }

      @media (max-width: 500px) {
         transform: translateX(500px);
      }

      @media (max-width: 400px) {
         transform: translateX(400px);
      }

      @media (max-width: 320px) {
         transform: translateX(320px);
      }
   }
            
   .menu {
      @media (min-width: 1200px) {
         display: none;
      }

      @media (max-width: 425px) {
         padding: 18px 0;
      }

      &.active {
         transform: translateX(0);
         z-index: 99;
      }

      &__logo {
         padding: 0 0 30px 0;
      }

      .logo__text {

         @media (max-width: 376px) {
            &_top {
               font-size: 16px;
            }

            &_bottom {
               font-size: 8px;
            }
         }  
      }

      &__nav {
         padding: 30px 0 0 0;
         border-top: 1px solid $color-light-gray;
      }
   
      .nav {
      
         &__list {}
   
         &__item {
            margin: 0 0 32px 0;

            &:last-child {
               margin: 0 0 60px 0;
            }
         }
   
         &__link {}
      }

      &__info {
         margin: 0 0 24px 0;
      }

      .info {
         display: flex;

         &__img {}
   
         &__box {}
   
         &__tel {}
   
         &__time {
   
            &_bold {}
         }
      }
   
      &__button {
         margin: 0 0 40px 0;
      }

      &__text {
         font-weight: 600;
         margin: 0 0 30px 0;

         @media (max-width: 340px) {
            font-size: 16px;
         }
      }

      &__list {
         margin: 0 0 20px 0;
      }

      .socnetwork {
         display: flex;

         @media (max-width: 500px) {
            justify-content: space-between;
         }
   
         &__item {
            border-radius: 50px;
            box-shadow: 0px 0px 12px 3px $color-gradient-red,
                  inset 0px 0px 0px 2px $color-gradient-purple;

            &:not(:last-child) {
               margin: 0 30px 0 0;

               @media (max-width: 500px) {
                  margin: 0;
               }
            }
         }
   
         &__link {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 20px;

            @media (max-width: 500px) {
               padding: 14px;
            }
         }
   
         &__img {
            width: 20px;
            height: 20px;
         }
      }
   }
}