.services-all {
   padding: 200px 0 20px 0;
   background-color: $color-dark-blue;

   &__links {
      position: relative;
      font-weight: 500;
      font-size: 16px;
      color: $color-lilac;
      z-index: 1;

      &__link {
         
         &:hover {
            background-image: linear-gradient(93.89deg, $color-gradient-red -1.38%, $color-gradient-purple 45.84%, $color-gradient-orange 100.94%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
         }
      }
   }

   &__decor-lines-right-to-bottom {
      top: 50%;
      right: 0;
   }
}