.consultation {
   position: relative;
   background-color: $color-dark-blue;
   padding: 186px 0 80px 0;
   display: flex;

   &__conteiner {
      position: relative;
   }

   &__decor-circles-bottom {
      left: 10px;
      bottom: 0;
      opacity: .7;
   }

   &__decor-circles-right {
      top: 0;
      right: 0;
   }

   &__decor-plus {
      bottom: -80px;
      right: -80px;
   }
}

.form {
   position: relative;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   background-color: rgb(24, 24, 39, .2);
   box-shadow: 0px 14px 44px #02040F, inset 0px 10px 62px #30385F;
   border-radius: 130px 20px 20px 20px;
   padding: 80px 160px;

   @media (max-width: 992px) {
      padding: 40px 80px;
   }

   @media (max-width: 600px) {
      padding: 40px;
   }

   @media (max-width: 400px) {
      padding: 20px;
   }

   &::after {
      content: "";
      position: absolute;
      top: -20px;
      left: 0;
      right: -20px;
      bottom: 0;
      background-image: url(../img/telegram-gradient.png);
      background-repeat: no-repeat;
      background-position: right top;

      @media (max-width: 992px) {
         top: -10px;
         right: -10px;
      }

      @media (max-width: 600px) {
         display: none;
      }
   }
   
   &__title {
      font-weight: 600;
      font-size: 36px;
      margin: 0 0 25px 0;
      text-align: center;
      z-index: 2;

      @media (max-width: 992px) {
         font-size: 30px;
      }

      @media (max-width: 400px) {
         font-size: 22px;
      }
   }

   &__subtitle {
      font-weight: 300;
      font-size: 20px;
      color: $color-gray;
      margin: 0 0 55px 0;
      max-width: 580px;
      text-align: center;
      z-index: 2;

      @media (max-width: 992px) {
         font-size: 16px;
      }

      @media (max-width: 400px) {
         font-size: 14px;
         margin: 0 0 30px 0;
      }
   }

   &__box {
      display: flex;
      z-index: 4;

      @media (max-width: 1200px) {
         flex-direction: column;
         align-items: center;
      }
   }

   &__input {
      border: none;
      outline: 1px solid $color-lilac;
      background-color: rgba(25, 25, 64, 0.6);
      border-radius: 50px;
      padding: 20px 0px 20px 60px;
      margin: 0 40px 0 0;
      color: $color-lilac;
      font-weight: 600;
      background-image: url(../img/tel.png);
      background-repeat: no-repeat;
      background-position: 22px center;

      @media (max-width: 1200px) {
         margin: 0 auto 30px auto;
      }

      @media (max-width: 400px) {
         font-size: 14px;
         padding: 10px 0 10px 40px;
         background-position: 10px center;
         width: 90%;
      }

      &::placeholder {
         color: $color-lilac;
      }
   }

   &__btn {
      border: none;
      color: inherit;

      &.button-gradient {
         padding: 18px 14px;
      }
   }
}