.button-transparent {
   display: inline-block;
   position: relative;
   font-size: 12px;
   font-weight: 700;
   padding: 14px 22px;
   text-transform: uppercase;
   box-shadow: 0px 0px 12px 3px $color-gradient-red,
         inset 0px 0px 0px 2px $color-gradient-purple;
   border-radius: 40px;
   transition: background .4s;

   &:hover {
      background: $color-gradient-red;
   }
}

.button-gradient {
   display: inline-block;
   position: relative;
   font-size: 15px;
   font-weight: 700;
   letter-spacing: .8px;
   padding: 22px 24px;
   background: linear-gradient(93.89deg, $color-gradient-red -1.38%, $color-gradient-purple 45.84%, $color-gradient-orange 100.94%);
   text-transform: uppercase;
   border-radius: 40px;
   transition: box-shadow .3s;

   &:hover {
      box-shadow: 0px 0px 12px 3px $color-gradient-red,
            inset 0px 0px 0px 2px $color-gradient-purple;
   }
}

.title {
   font-weight: 500;
   font-size: 50px;
   color: $color-white;

   @media (max-width: 992px) {
      font-size: 38px;
   }

   @media (max-width: 400px) {
      font-size: 28px;
   }
}

.logo {
   display: flex;
   align-items: center;
   
   &__text {
      font-weight: 500;
      display: flex;
      flex-direction: column;
      align-items: center;

      &_top {
         font-size: 24px;
         margin: 0 0 10px 0;
      }
   
      &_bottom {
         font-size: 10px;
         text-transform: uppercase;
      }  
   }

   img {
      width: 60px;
      height: 60px;
      object-fit: cover;
      margin: 0 10px 0 0;

      @media (max-width: 375px) {
         width: 30px;
         height: 30px;
      }
   }
}

.socnetwork {
   display: flex;
   width: 100%;

   @media (max-width: 500px) {
      justify-content: space-between;
   }

   &__item {
      border-radius: 50px;
      box-shadow: 0px 0px 12px 3px $color-gradient-red,
            inset 0px 0px 0px 2px $color-gradient-purple;

      &:not(:last-child) {
         margin: 0 30px 0 0;

         @media (max-width: 500px) {
            margin: 0;
         }
      }
   }

   &__link {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 20px;

      @media (max-width: 500px) {
         padding: 14px;
      }
   }

   &__img {
      width: 20px;
      height: 20px;
   }
}

.decor {
   position: absolute;
   z-index: 1;
   pointer-events: none;
}

.btn-scroll-up {
   position: fixed;
   bottom: 40px;
   right: 40px;
   z-index: 99;
   display: flex;
   align-items: center;
   justify-content: center;
   cursor: pointer;
   width: 50px;
   height: 50px;
   border-radius: 100%;
   background: linear-gradient(93.89deg, $color-gradient-red -1.38%, $color-gradient-purple 45.84%, $color-gradient-orange 100.94%);
   transition: box-shadow .4s, opacity .4s;
   opacity: 1;

   @media (max-width: 425px) {
      right: 10px;
      bottom: 10px;
   }

   &.hide {
      opacity: 0;
   }

   &:hover {
      box-shadow: 0px 0px 12px 3px $color-gradient-red,
            inset 0px 0px 0px 2px $color-gradient-purple;
   }

   &__img {
      width: 20px;
      height: 20px;
      transform: rotate(180deg);
      transition: transform .3s;
   }

   &:hover &__img {
      transform: rotate(180deg) translateY(5px);
   }
}

::-webkit-scrollbar {
   width: 10px;

   &-track {
      background-color: $color-dark-blue;
   }

   &-thumb {
      background: linear-gradient(93.89deg, $color-gradient-red -1.38%, $color-gradient-purple 45.84%, $color-gradient-orange 100.94%);
      border-radius: 50px;
   }
}

.cards {
   display: flex;
   flex-wrap: wrap;
   gap: 25px;

   @media (max-width: 700px) {
      gap: 20px;
   }
}

.card {
   display: flex;
   flex: 1 0 31%;
   background-color: transparent;
   border: 2px solid $color-blue;
   border-radius: 12px;
   padding: 25px;
   transition: all .3s !important;

   &:hover {
      box-shadow: 0px 0px 12px 3px $color-gradient-red,
            inset 0px 0px 0px 2px $color-gradient-purple;
      border: 2px solid $color-gradient-purple;
      transition: all .3s;
   }

   @media (max-width: 1200px) {
      flex-direction: column;
   }

   @media (max-width: 700px) {
      flex: 3 0 40%;
   }

   @media (max-width: 600px) {
      flex: 3 0 50%;
      flex-direction: row;
      padding: 18px;
   }

   @media (max-width: 400px) {
      flex-direction: column;
   }

   &__img {
      margin: 0 24px 0 0;
      max-width: 52px;
      max-height: 52px;

      @media (max-width: 1200px) {
         margin: 0 0 20px 0;  
      }

      @media (max-width: 600px) {
         margin: 0 20px 0 0;  
      }

      @media (max-width: 400px) {
         margin: 0 0 20px 0;  
      }
   }

   &__text {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      flex: 1 1 auto;
   }

   &__title {
      font-weight: 600;
      margin: 0 0 10px 0;
      flex: 1 0 auto;

      @media (max-width: 700px) {
         font-size: 18px;
      }
   }

   &__subtitle {
      font-size: 15px;
      line-height: 20px;
      margin: 0 0 15px 0;
      max-width: 270px;
   }

   &__link {
      position: relative;
      font-weight: 700;
      font-size: 14px;
      text-transform: uppercase;
      background-image: linear-gradient(93.89deg, $color-gradient-red -1.38%, $color-gradient-purple 45.84%, $color-gradient-orange 100.94%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;

      &::after {
         content: "";
         position: absolute;
         top: 0;
         left: 0;
         right: -20px;
         bottom: 0px;
         background-image: url(../img/arrow-gradient.png);
         background-position: right center;
         background-repeat: no-repeat;
         transition: transform .3s;
      }

      &:hover.card__link::after {
         transform: translateX(10px);
      }
   }
}