.services {
   position: relative;;
   background-color: $color-dark-blue;
   padding: 110px 0 10px 0;

   &__decor-lines-top-to-right {
      top: 50px;
      left: 0;
   }
}

.tabs {

   &__nav {
      display: flex;
      align-items: center;
      position: relative;
      
      @media (max-width: 992px) {
         display: none;  
      }
      
      &::after {
         content: '';
         position: absolute;
         left: -1000px;
         right: -1000px;
         bottom: -25px;
         height: 1px;
         background-color: $color-blue;
      }

      & .button-transparent {
         margin-left: auto;
      }
   }

   &__nav-btn {
      position: relative;
      font-size: 24px;
      font-weight: 500;
      color: $color-lilac;
      padding: 0;
      border: none;
      background-color: transparent;
      margin: 0 60px 0 0;
      transition: color .4s;

      &::after {
         content: '';
         position: absolute;
         left: 0;
         right: 0;
         bottom: -32px;
         height: 0px;
         border-radius: 4px;
         background: linear-gradient(93.89deg, $color-gradient-red -1.38%, $color-gradient-purple 45.84%, $color-gradient-orange 100.94%);
         transition: height .4s ease-in-out;
      }

      &:last-child {
         margin: 0;
      }

      &.active {
         position: relative;
         color: $color-white;

         &::after {
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            bottom: -32px;
            height: 6px;
            border-radius: 4px;
            background: linear-gradient(93.89deg, $color-gradient-red -1.38%, $color-gradient-purple 45.84%, $color-gradient-orange 100.94%);
         }
      }
   }

   &__content {
      padding: 70px 0 0 0; 
      display: flex;
      flex-direction: column;

      & .button-transparent {
         display: none;

         @media (max-width: 992px) {
            display: inline-block;
            margin: 30px auto 0 auto;
         }
      }

      @media (max-width: 992px) {
         padding: 0;
      }
   }

   .tabs__item__cards {
      display: none;

      &.active {
         display: flex;
      }
   }
}  